import React, { Component } from "react";
import "./App.css";
import Dashboard from "./Dashboard";
import Card from "./Card/Card";
class App extends Component {
	state = {
		persons: [{ name: "Char", age: 69 }, { name: "Nel", age: 420 }],

		arr: [],
		code: ["I", " ", "L", "O", "V", "E", " ", "C", "O", "C", "K"],
		code1: ["I", " ", "L", "O", "V", "E", " ", "G", "A", "Y", "S"]
	};
	switchNameHandler = (newName1, newName2) => {
	
		this.setState({
			persons: [{ name: newName1, age: 69 }, { name: newName2, age: 420 }]
		});
	};

	nameChangeHandler = event => {
		this.setState({
			persons: [
				{ name: this.state.persons[0].name, age: 69 },
				{ name: event.target.value, age: 420 }
			]
		});
	};

	onKeyPressed = e => {
		var currentState = this.state.arr;
		if (this.state.arr.length === 11) this.state.arr.shift();
		currentState.push(String(e.key).toUpperCase());
		this.setState({ arr: currentState });

	
	};


	render() {
		let page1 = null;
		let page2 = null;

		if (JSON.stringify(this.state.arr) === JSON.stringify(this.state.code)) {
			page1 = (
				<Dashboard
					onKeyDown={this.onKeyPressed}
					tabIndex="0"
					
				/>
			);
		} else if (
			JSON.stringify(this.state.arr) === JSON.stringify(this.state.code1)
		) {
			page2 = (
				<Card
					onKeyDown={this.onKeyPressed}
					className="App"
					name={this.state.persons[0].name}
					age={this.state.persons[0].age}
				></Card>
			);
		}

		return (
			<div>
				{page1}

				{page2}

				<div
					onKeyDown={this.onKeyPressed}
					tabIndex="0"
					className="App"
	
				></div>


	
			</div>
		);
	}
}

export default App;
