import React from 'react';
//import './App.css';



const card = (props) => {

return( 
<div onClick = {props.click} className = "container">
    
<div  className = "card"> I am {props.name} and I love D {props.age} </div>
  {props.children} 
  <input type = "text" onChange = {props.change} value = {props.name}/>

</div>

)


}

export default card;
