import React from 'react';
import Link from '@material-ui/core/Link';
import { makeStyles } from '@material-ui/core/styles';
import Table from '@material-ui/core/Table';
import TableBody from '@material-ui/core/TableBody';
import TableCell from '@material-ui/core/TableCell';
import TableHead from '@material-ui/core/TableHead';
import TableRow from '@material-ui/core/TableRow';
import Title from './Title';

// Generate Order Data
function createData(id, date, name, shipTo, paymentMethod, amount) {
  return { id, date, name, shipTo, paymentMethod, amount };
}

const rows = [
  createData(0, '16 Mar, 2019', '69.69.69.69', "Smart Lock", "Unlock", "Nel" ),
  createData(1, '16 Mar, 2019', '69.69.69.69',"Smart Lock", "Lock", "Char" ),
  createData(2, '16 Mar, 2019', '69.69.69.69',"Smart Outlet", "Off", "Nel" ),
  createData(3, '16 Mar, 2019', '69.69.69.69',"Smart Outlet", "On", "Char"),
  createData(4, '15 Mar, 2019', '69.69.69.69', "Gay Lock", "Dick" ,"Nel" ),
];

const useStyles = makeStyles(theme => ({
  seeMore: {
    marginTop: theme.spacing(3),
  },
}));

export default function Orders() {
  const classes = useStyles();
  return (
    <React.Fragment>
      <Title>Recent Activity</Title>
      <Table size="small">
        <TableHead>
          <TableRow>
            <TableCell>Date</TableCell>
            <TableCell>IP</TableCell>
            <TableCell>Device</TableCell>
            <TableCell>Action</TableCell>
            <TableCell align="right">Name</TableCell>
          </TableRow>
        </TableHead>
        <TableBody>
          {rows.map(row => (
            <TableRow key={row.id}>
              <TableCell>{row.date}</TableCell>
              <TableCell>{row.name}</TableCell>
              <TableCell>{row.shipTo}</TableCell>
              <TableCell>{row.paymentMethod}</TableCell>
              <TableCell align="right">{row.amount}</TableCell>
            </TableRow>
          ))}
        </TableBody>
      </Table>
      <div className={classes.seeMore}>
        <Link color="primary" href="javascript:;">
          See more
        </Link>
      </div>
    </React.Fragment>
  );
}
