import React, { useEffect } from "react";
import clsx from "clsx";
import { makeStyles } from "@material-ui/core/styles";
import Paper from "@material-ui/core/Paper";
import Button from "@material-ui/core/Button";

const useStyles = makeStyles(theme => ({
	paper: {
		padding: theme.spacing(2),
		display: "flex",
		overflow: "auto",
		flexDirection: "column"
	},
	fixedHeight: {
		height: 240
	},
	lightPaperOn: {
		padding: theme.spacing(2),
		display: "flex",
		overflow: "auto",
		flexDirection: "column",
		margin: "auto"
	},
	lightPaperBackgroundOn: {
		background: "linear-gradient(45deg, Khaki 30%, White 90%)",
		borderRadius: 3,
		border: 0,
		color: "white",
		padding: "0 30px",
		boxShadow: "0 3px 5px 2px rgba(0, 0, 0, .3)"
	},
	lightPaperBackgroundOff: {
		background: "linear-gradient(45deg, DarkSlateGray 30%, Khaki 90%)",
		borderRadius: 3,
		border: 0,
		color: "white",
		padding: "0 30px",
		boxShadow: "0 3px 5px 2px rgba(0, 0, 0, .3)"
	},
	fixedHeightLights: {
		height: 100
	},
	button: {
		display: "flex",
		margin: "auto"
	}
}));

export default function LivingRoomLights() {
	const classes = useStyles();
	const [isLightOn, setLightState] = React.useState(false);
	useEffect(() => {
		checkInitialLightState();
	}, []);

	const lightPaper = clsx(classes.lightPaperOn, classes.fixedHeightLights);

	// LightToggle queries the current state of lights and sends a POST request to toggle them off.
	const LightToggle = () => {
		var currentState = isLightOn;
		var lightRequest;
		const http = new XMLHttpRequest();
		const url =
			"https://use1-wap.tplinkcloud.com/?token=e889682e-A5eVhTfZ999VCaq9rGqfZT7";
		http.open("POST", url);
		http.setRequestHeader("Content-Type", "application/json", true);
		if (currentState) {
			lightRequest = '{"system":{"set_relay_state":{"state":0}}}';
		} else {
			lightRequest = '{"system":{"set_relay_state":{"state":1}}}';
		}
		const body = JSON.stringify({
			method: "passthrough",
			params: {
				deviceId: "80068B8DFB6165F50793429DC0AE99C51B6B719E",
				requestData: lightRequest
			}
		});
		http.onreadystatechange = () => {
			if (http.readyState === 4 && http.status === 200) {
				setLightState(!currentState);
			}
		};
		http.send(body);
	};

	// checkInitialLightState runs once at the initialization of the element and checks the current state of the light from the TPLINK Cloud website.
	const checkInitialLightState = () => {
		const http = new XMLHttpRequest();
		const url =
			"https://use1-wap.tplinkcloud.com/?token=e889682e-A5eVhTfZ999VCaq9rGqfZT7";
		http.open("POST", url);
		http.setRequestHeader("Content-Type", "application/json", true);
		const body = JSON.stringify({
			method: "passthrough",
			params: {
				deviceId: "80068B8DFB6165F50793429DC0AE99C51B6B719E",
				requestData: '{"system":{"get_sysinfo":null}}'
			}
		});
		http.onreadystatechange = () => {
			if (http.readyState === 4 && http.status === 200) {
				const parseHelp = JSON.parse(http.response);
				const systemState = JSON.parse(parseHelp.result.responseData);
				setLightState(
					systemState.system.get_sysinfo.relay_state === 1 ? true : false
				);
			}
		};
		http.send(body);
	};

	return (
		<div>
			<Paper
				className={lightPaper}
				classes={{
					root: isLightOn
						? classes.lightPaperBackgroundOn
						: classes.lightPaperBackgroundOff
				}}
			>
				<Button
					onClick={LightToggle}
					className={classes.button}
					classes={{
						root: isLightOn
							? classes.lightPaperBackgroundOff
							: classes.lightPaperBackgroundOn
					}}
				>
					<font color={isLightOn ? "#dce5fc" : "#403f39"}>
						<strong>Living Room Lights</strong>
					</font>
				</Button>
			</Paper>
		</div>
	);
}
